import axios from "axios";
import storageUtils from "@/commonutils/storageUtils";
import DonMessage from "@/commonutils/message";
import router from "@/router";
const Message = new DonMessage();
/**
 * token
 */
axios.interceptors.request.use(
  function(config) {
    const storage_user = storageUtils.getUser();
    if (storage_user && config.data) {
      if (!config.data.token || JSON.stringify(config.data.token) === "{}") {
        config.data.token = storage_user.token;
      }
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  }
);
/**
 * http
 */
axios.interceptors.response.use(
  response => {
    if (
      response.data &&
      response.data.code === parseInt(process.env.VUE_APP_BASE_ERR_TOKEN_CODE)
    ) {
      Message.warning("");
      setTimeout(() => {
        router.replace({
          path: "/login"
        });
      }, 1000);
    }
    return Promise.resolve(response);
  },
  error => {
    const status = error.response;
    if (status) {
      switch (status.status) {
        case 400:
          error.message = "";
          break;
        case 401:
          error.message = "";
          break;
        case 403:
          error.message = "";
          break;
        case 404:
          error.message = ",";
          break;
        case 405:
          error.message = "";
          break;
        case 408:
          error.message = "";
          break;
        case 500:
          error.message = "";
          break;
        case 501:
          error.message = "";
          break;
        case 502:
          error.message = "";
          break;
        case 503:
          error.message = "";
          break;
        case 504:
          error.message = "";
          break;
        case 505:
          error.message = "http";
          break;
        default:
          error.message = `${error.response.status}`;
      }
    } else {
      error.message = "";
    }
    return Promise.reject(error);
  }
);
/**
 * axios
 * @param {String} url // api
 * @param {Object} data // 
 * @param {String} type // 
 * @param {Object} responseType // 
 * @param {Number} timeout //  ()
 * @returns {Promise<unknown>} 
 */
export default function ajax(
  url,
  data = {},
  type = "GET",
  responseType = undefined,
  timeout = 200000
) {
  return new Promise((resolve, reject) => {
    let promise;
    axios.defaults.timeout = timeout; // 
    if (type === "GET") {
      promise = axios.get(url, {
        params: data
      });
    } else {
      promise = axios.post(url, data, responseType);
    }
    promise
      .then(
        response => {
          resolve(response.data);
        },
        error => {
          resolve({
            code: "error",
            msg: `${url}${error.message}`
          });
        }
      )
      .catch(err => {
        Message.error(err.message);
        reject(err);
      });
  });
}
